@media screen and (max-width: 767px) {
    .nav__list {
        display: none;
    }

    .nav__contact {
        display: none;
    }

    .header {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .header__menu {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .mobile-menu {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(17, 21, 24, 0.9);
        overflow: auto;
    }

    .mobile-menu__close-btn {
        position: absolute;
        top: 20px;
        right: 20px;
        background-color: transparent;
        border: none;
        padding: 0;
        line-height: 0;

        &:hover,
        :focus {
            fill: #FF6B09;
        }
    }

    .mobile-menu__container {
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .mobile-menu__list {
        margin-top: 90px;
        margin-bottom: 40px;
    }

    .mobile-menu__link {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 180%;
        letter-spacing: 0.03em;
        color: #FFFFFF;

        &:hover,
        :focus {
            color: #FF6B09;
        }

        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .mobile-menu-nav__contact {
        max-width: 280px;
        margin: auto;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 15px;
        display: block;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FF6B09;
        border: 2px solid #FF6B09;
        padding-top: 14.5px;
        padding-bottom: 14.5px;
        padding-right: 40px;
        padding-left: 40px;

        &:hover,
        :focus {
            box-shadow: 0 10px 10px -10px black, 0 -10px 10px -10px black;
        }

        transition: 0.2s ease;
    }

    .header-menu__button {
        background-color: transparent;
        border: none;
        padding: 0;
        line-height: 0;

        &:hover,
        :focus {
            fill: #FF6B09;
        }

        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .mobile-menu__content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

}

@media screen and (min-width: 768px) {
    .nav__list {
        display: none;
    }

    .mobile-menu-nav__contact {
        display: none;
    }

    .header {
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .nav__contact {
        max-width: 280px;
        margin-left: 304px;
        margin-right: 40px;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 15px;
        display: block;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FF6B09;
        border: 2px solid #FF6B09;
        padding-top: 14.5px;
        padding-bottom: 14.5px;
        padding-right: 5px;
        padding-left: 5px;

        &:hover,
        :focus {
            box-shadow: 0 10px 10px -10px black, 0 -10px 10px -10px black;
        }

        transition: 0.2s ease;
    }

    .header__menu {
        display: flex;
    }

    .header-menu__button {
        background-color: transparent;
        border: none;
        padding: 0;
        line-height: 0;

        &:hover,
        :focus {
            fill: #FF6B09;
        }

        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .mobile-menu {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(17, 21, 24, 0.9);
        overflow: auto;
    }

    .mobile-menu__close-btn {
        position: absolute;
        top: 56px;
        right: 32px;
        background-color: transparent;
        border: none;
        padding: 0;
        line-height: 0;

        &:hover,
        :focus {
            fill: #FF6B09;
        }

        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .mobile-menu__container {
        position: relative;
        padding-top: 40px;
        padding-bottom: 40px;
    }

    .mobile-menu__list {
        position: absolute;
        top: 165px;
        padding-bottom: 0;
        padding-left: 216px;
        padding-right: 220px;

    }

    .mobile-menu__link {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 180%;
        letter-spacing: 0.03em;
        color: #FFFFFF;

        &:hover,
        :focus {
            color: #FF6B09;
        }

        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .mobile-menu__link {
        font-size: 26px;
    }

}

@media screen and (min-width: 1680px) {
    .header-menu__button {
        display: none;
    }

    .nav__list {
        display: flex;
        align-items: center;
    }

    .nav-list__link {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        text-transform: uppercase;
        color: #B9B9B9;

        &:hover,
        :focus {
            color: #FF6B09;
        }

        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .header__menu {
        display: flex;
        justify-content: space-between;
    }

    .navigation {
        display: flex;
    }

    .nav-list__item {
        padding-right: 40px;
    }

    .nav__contact {
        max-width: 336px;
        width: 336px;
        margin: 0;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 15px;
        display: block;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #FF6B09;
        border: 2px solid #FF6B09;
        padding-top: 26px;
        padding-bottom: 26px;
        padding-right: 24px;
        padding-left: 24px;

        &:hover,
        :focus {
            box-shadow: 0 10px 10px -10px black, 0 -10px 10px -10px black;
        }

        transition: 0.2s ease;
    }

    .current {
        color: #FF6B09;
    }

}