.article {
  
       padding: 0 20px;
    @media screen and (min-width:768px) {
         display: flex; 
         padding: 40px 50px;
    }
    @media screen and (min-width:1680px) {
         padding-top: 60px;
    padding-left: 80px;
    padding-bottom: 60px;
    }
}
.slider__icon {
    display: block;
    margin: 0 auto;
}
.review__btn {
    display: none;
}
.thumb-review {
    width: 240px; 
    text-align: center;
 @media screen and (min-width:768px) {
  width: 412px; 
  text-align: left;
    }
    @media screen and (min-width:1680px) {
  width: 706px; 
    }
}
.thumb-photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media screen and (min-width:768px) {
        justify-content: flex-start;
     margin-right: 44px;   
    }
    @media screen and (min-width:1680px) {
     margin-right: 40px;   
    }
}
.thumb-photo img {
    margin-bottom: 24px;
    width: 90px;
     @media screen and (min-width:768px) {
      margin-bottom: 8px;   
      width: 150px;
     }

}
.slider__icon {
    margin-bottom: 24px;
}

.review__student {
    margin-bottom: 16px;
    font-weight: 800;
font-size: 14px;
line-height: 1.21;
letter-spacing: 0.08em;
color: #FF6B09;
    @media screen and (min-width:768px) {
        margin-bottom: 20px;
      font-size: 20px;
line-height: 1.2;
    } 
    @media screen and (min-width:1680px) {
      margin-bottom: 8px;
font-size: 30px;
line-height: 1.23;   
    }
}

.review__student_education {
    margin-bottom: 16px;
    font-weight: 500;
font-size: 12px;
line-height: 1.25;
letter-spacing: 0.08em;
color: #B9B9B9;
     @media screen and (min-width:768px)  {
         margin-bottom: 20px;
     font-size: 12px;
line-height: 1.25;
     }
     @media screen and (min-width:1680px) {
          margin-bottom: 20px;
font-size: 14px;
line-height: 1.21;  
     }
    
}
.review__text {
  font-weight: 500;
font-size: 12px;
line-height: 1.67;
color: #3D3D3D; 
 @media screen and (min-width:768px) {
font-size: 12px;
line-height: 20px;
 }
 @media screen and (min-width:1680px) {
  font-size: 18px;
line-height: 1.56;   
 }
}
.review__btn {
    color: #FF6B09;
}
.slick-list {
    overflow: hidden;
}

.slider-pagination .slick-list {
    width: 208px;
    height: 60px;
    @media screen and (min-width:768px) { 
    width: 570px;
    } 
}
.slick-track{
    display: flex;
}
.slider .slick-list {
    width: 280px;
    @media screen and (min-width:768px) { 
    width: 704px;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04); 
    }   
    @media screen and (min-width:1680px) { 
    width: 1142px;
    }   
}
.slider {
    width: 280px;
    margin: 0 auto;
    position: relative;
    @media screen and (min-width:768px) {
        width: 704px;
         padding: 0 33px;
    }
    @media screen and (min-width:1680px) {
        width: 1203px;
         padding: 0 33px;
    }
}
.slider-pagination  {
width: 225px;
    margin: 0 auto;
    position: relative;
    @media screen and (min-width:768px) {
        width: 570px;
    }
}
// .slider-pagination__item {
//     padding: 0 6px;
// }
.slider .slick-arrow, .slider-pagination .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
z-index: 10;
font-size: 0;
width: 27px;
height: 16px;
border: none;
}
.slider .slick-arrow.slick-prev, .slider-pagination .slick-arrow.slick-prev {
    left: 0;
    background: url('..//images/arrow1.svg') 0 0 / 100% no-repeat;
}
@media screen and (max-width:767px) {
  .slider .slick-arrow.slick-prev, .slider .slick-arrow.slick-next {
display: none;
}  
}

.slider .slick-arrow.slick-next, .slider-pagination .slick-arrow.slick-next {
    right: 0;
    background: url('..//images/arrow2.svg') 0 0 / 100% no-repeat;
}
.slider .slick-arrow.slick-disabled, .slider-pagination .slick-arrow.slick-disabled{
    opacity: .2;
}
